* {
	margin: 0;
	box-sizing: border-box;
	font-family: "Monotype Corsiva";
    font-style: italic;
    letter-spacing: 5px;
    font-weight: bold;
}
body{
    zoom:90%;
    background-color: #f9d1b7;
    background-image: linear-gradient(315deg, #f9d1b7 0%, #f894a4 74%);
    background-position: center;
    background-attachment: fixed;
    height:100vh;

}
header {
	display: flex;
	justify-content: center;
	padding: 15px;
}
.heading{
    margin-top: 30px;
}
h1 {
	font-size: 40px;
	font-weight: 600;
	text-align: center;
    text-decoration: crimson dotted underline;
}
.curb{
    margin-top: 5px;
	font-size: 24px;
	font-weight: 900;
   
    padding: 5px;
    letter-spacing: 5px;
	background-color: lemonchiffon;
	border-radius: 8px;
    text-align: center;
    border: 3px solid black;
}
.incform{
    display: inline-block;
	margin: 17px;
	position: relative;
    left:18%;
    padding:40px;
    border: 5px solid black;
    background-color: lightcoral;
    align-items: center;
    justify-content: center;
  
}

.incform input{
    margin:5px;
    border:3px solid black;
    font-weight: bold;
}

.incform:after{
    content: "";
	position: absolute;
	top: 0px;
	left: 0px;
	right: 0px;
	bottom: 0px;
	z-index: 0;
	background-image: linear-gradient(to right, #de6262 ,#ffb88c);
	border-radius: 120px;
	transition: 0.2s;
    color:black;
}
.incform:focus-within:after {
	top: -3px;
	left: -3px;
	right: -3px;
	bottom: -3px;
}
.inform{
    position: relative;
	z-index: 1;
	display: flex;
	justify-content: center;
	transition: 0.4s;
	border-radius: 8px;
}
.inform input {
	font-size: 19px;
	padding: 10px 15px;
    text-align: center;
    width:320px;
    background-color: peachpuff;
	
}
.inform input[type="submit"] {
	border-radius: 36%;
	cursor: pointer;
	text-transform: uppercase;
	transition:color 0.6s ease-out;
    background-image: linear-gradient(to top,#eecda3, #ef629f);
  
    
}
.inform input[type="submit"]:hover {
	opacity: 0.8;
    color:brown;
    background-color: orange;
   
}
.inform input[type="submit"]:active {
	transform: scale(0.8);
   
   
}

.inlist{
    display:inline-block;
    position: relative;
    left:19%;
    padding: 20px;
}


.incitem {
	position: relative;
	padding: 3px;
	background-color: sandybrown;
	border-radius: 5px;
    border:2px solid black;
	margin-bottom: 15px;
	display: flex;
	transition: 0.4s;
    
    color:black;
   
}
.entry{
    
    display: flex;
    text-align: center;
    border-radius: 0.5rem;
    margin-bottom: 0.7rem;
    margin-left: 18rem;
    min-width: 45rem;
    letter-spacing: 2px;
    text-transform: capitalize;
    justify-content: space-between;
    align-items: center;
   
}
table{
    width:300px;
    text-align: center;
    position: relative;
    align-items: center;
    justify-content: center;
    margin-top: 5px;
    left:10%;
    padding:3px;
   
}

td:hover {
    background-color: coral;
}

td{
    border: 2px solid black;
    padding:5px;
    font-size: 20px;
   
}
.space{
    border: none;
    background-color: none;
}

.space :hover{
    border: none;
    background-color: none;
}
